import React,{Component} from "react";
import "./board.css";

class Board extends Component{
	constructor(props){
		super(props);
		this.state = {
			canvas: "",
			spriteSheet: "",
			context: ""
		}

		this.canvasRef = React.createRef();
		this.spriteSheetRef = React.createRef();
	}

	componentDidMount() {
		var canvas = this.canvasRef.current;
		var ctx = canvas.getContext("2d");
		var spriteSheet = this.spriteSheetRef.current;
		spriteSheet.onload = () => {
			this.drawTiles(this.props.tiles,this.props.tileSize,this.state.context,this.state.spriteSheet, this.props.zoom);
		}

		this.setState({canvas: canvas, spriteSheet: spriteSheet, context: ctx});
	}

	drawCharacters = (tiles,tileSize,characters,context,spriteSheet,zoom) =>{
		characters.forEach(char => {
			var x = char.x;
			var y = char.y;

			var canvasX = char.position[0] * tileSize * zoom;
			var canvasY = char.position[1] * tileSize * zoom;
			var canvasTileSize = tileSize*zoom;
			
			context.drawImage(
				spriteSheet,
				x,
				y,
				tileSize,
				tileSize,
				canvasX,
				canvasY,
				canvasTileSize,
				canvasTileSize
			);
		});
	}

	drawTiles = (tiles,tileSize,context,spriteSheet,zoom) =>{
		tiles.map((row,indexR) => {
			row.map((col,indexC) =>{
				console.log(col);
				console.log(spriteSheet);
				var x = col.x;
				var y = col.y;
				var canvasX = indexC * tileSize * zoom;
				var canvasY = indexR * tileSize * zoom;
				
				var canvasTileSize = tileSize*zoom;
				context.drawImage(
					spriteSheet,
					x,
					y,
					tileSize,
					tileSize,
					canvasX,
					canvasY,
					canvasTileSize,
					canvasTileSize
				);
				
			});
		});
	}

	render(){
		return(
			<div>
			<canvas ref={this.canvasRef} width={this.props.width} height={this.props.height} onMouseMove={this.props.handleMouseMove} onClick={this.props.handleClick} />
			<img ref={this.spriteSheetRef} src={require(`../../tileSheet.png`)} alt="whoops, can't find it!" />
			</div>
		);
	}
	
};

export default Board;


